<template>
  <div class="wapper-box">
    <div class="search-box">
      <div class="time-box">
        <div
          class="start-time"
          :class="{ 'placeholder-text': !startTime }"
          @click="openTime('start')"
        >
          <div class="time-text-box">{{ startTime || "请选择开始时间" }}</div>
          <img
            src="@/assets/images/icon-zhong.svg"
            class="icon-zhong"
            alt=""
            srcset=""
          />
        </div>
        <div
          class="start-time"
          :class="{ 'placeholder-text': !endTime }"
          @click="openTime('end')"
        >
          <div class="time-text-box">{{ endTime || "请选择结束时间" }}</div>
          <img
            src="@/assets/images/icon-zhong.svg"
            class="icon-zhong"
            alt=""
            srcset=""
          />
        </div>
      </div>
      <div class="search-input">
        <div class="input-box">
          <input
            v-model="keyword"
            type="text"
            class="input-text"
            placeholder="请输入您要查询的名字"
          />
        </div>
        <van-popover
          v-model="showPopover"
          trigger="click"
          :actions="actions"
          @select="onSelect"
        >
          <template #reference>
            <div class="popover-text">{{ popoverText }}</div>
          </template>
        </van-popover>
        <van-button type="info" class="search-btn" @click="searchHandle"
          >搜索</van-button
        >
        <span class="uploadBtn">下载excel</span>
      </div>
    </div>
    <div class="table-box">
      <div class="table-title">入款({{ bills_in.length }}笔)</div>
      <el-table
        :data="bills_in"
        border
        style="width: 100%"
        :max-height="tableHeight"
      >
        <el-table-column prop="time" label="时间" align="center">
        </el-table-column>
        <el-table-column prop="amount" label="金额" align="center">
          <template slot-scope="scope">
            <span>
              {{ scope.row.amount_num }} ({{ scope.row.amount_unit }})
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="rate" label="费率" align="center">
            </el-table-column>
            <el-table-column prop="usd_rate" label="汇率" align="center">
            </el-table-column>
        <el-table-column prop="after_rate_amount" label="结算" align="center">
          <template slot-scope="scope">
            <span>
              {{ scope.row.after_rate_amount_num }} ({{
                scope.row.after_rate_amount_unit
              }})
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="other_name" label="标记人" align="center">
        </el-table-column>
        <el-table-column prop="name" label="操作人" align="center">
        </el-table-column>
        <el-table-column prop="remarik" label="备注" align="center">
        </el-table-column>
        <template slot="empty">
          <el-empty description="暂无数据"></el-empty>
        </template>
      </el-table>
    </div>
    <div class="table-box">
      <div class="table-title">下发({{ bills_out.length }}笔)</div>
      <el-table
        :data="bills_out"
        border
        style="width: 100%"
        :max-height="tableHeight"
      >
        <el-table-column prop="time" label="时间" align="center">
        </el-table-column>

        <el-table-column prop="amount" label="金额" align="center">
          <template slot-scope="scope">
            <span>
              {{ scope.row.amount_num }} ({{ scope.row.amount_unit }})
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="rate" label="费率" align="center">
            </el-table-column>
            <el-table-column prop="usd_rate" label="汇率" align="center">
            </el-table-column>
        <el-table-column prop="after_rate_amount" label="结算" align="center">
          <template slot-scope="scope">
            <span>
              {{ scope.row.after_rate_amount_num }} ({{
                scope.row.after_rate_amount_unit
              }})
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="other_name" label="标记人" align="center">
        </el-table-column>
        <el-table-column prop="name" label="操作人" align="center">
        </el-table-column>
        <el-table-column prop="remarik" label="备注" align="center">
        </el-table-column>
        <template slot="empty">
          <el-empty description="暂无数据"></el-empty>
        </template>
      </el-table>
    </div>
    <div class="table-box" v-if="dataType === 1">
      <div class="table-title">记账分类</div>
      <van-collapse v-model="activeNames">
        <van-collapse-item
          name="1"
        >
        <div class="collapse-tilte" slot="title">
            <span>统计（按标记人） ({{markList.length}}人)</span>
            <span class="collapse-icon">{{activeNames.indexOf('1') > -1 ? '收起' : '展开'}}</span>
          </div>
          <div class="table-collapse-box">
            <el-table
              :data="markList"
              border
              style="width: 100%"
              :max-height="tableHeight"
            >
              <el-table-column prop="name" label="用户名" align="center">
              </el-table-column>
              <el-table-column prop="sum_amount" label="入款" align="center">
              </el-table-column>
              <el-table-column
                prop="before_rate_amount"
                label="应下发"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="after_rate_amount"
                label="已下发"
                align="center"
              >
              </el-table-column>
              <template slot="empty">
                <el-empty description="暂无数据"></el-empty>
              </template>
            </el-table>
          </div>
        </van-collapse-item>
        <van-collapse-item
          :title="'统计（按操作人） (' + operatorList.length + '人)'"
          name="2"
        >
        <div class="collapse-tilte" slot="title">
            <span>统计（按操作人） ({{operatorList.length}}人)</span>
            <span class="collapse-icon">{{activeNames.indexOf('2') > -1 ? '收起' : '展开'}}</span>
          </div>
          <div class="table-collapse-box">
            <el-table
              :data="operatorList"
              border
              style="width: 100%"
              :max-height="tableHeight"
            >
              <el-table-column prop="name" label="用户名" align="center">
              </el-table-column>
              <el-table-column prop="sum_amount" label="入款" align="center">
              </el-table-column>
              <el-table-column
                prop="before_rate_amount"
                label="应下发"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="after_rate_amount"
                label="已下发"
                align="center"
              >
              </el-table-column>
              <template slot="empty">
                <el-empty description="暂无数据"></el-empty>
              </template>
            </el-table>
          </div>
        </van-collapse-item>
        <!-- <van-collapse-item title="统计（按汇率分类）" name="3">
          <div class="table-collapse-box">
            <el-table
              :data="tableData"
              border
              style="width: 100%"
              :height="tableHeight"
            >
              <el-table-column prop="date" label="汇率" align="center">
              </el-table-column>
              <el-table-column prop="name" label="入款" align="center">
              </el-table-column>
              <el-table-column prop="address" label="换算U" align="center">
              </el-table-column>
              <template slot="empty">
                <el-empty description="暂无数据"></el-empty>
              </template>
            </el-table>
          </div>
        </van-collapse-item> -->
      </van-collapse>
    </div>
    
    <div class="table-box" v-if=" statisticsObject.totalIncomeRMB > 0 || statisticsObject.totalIncomeUSDT  > 0">
      <div class="tatol-title">总计</div>
      <div class="descriptions-box">
        <el-descriptions :column="1" border>
          <el-descriptions-item label="汇率：">
            <span class="data-item">{{ statisticsObject.usd_rate }}</span>
          </el-descriptions-item>
          <el-descriptions-item label="费率：">
            <span class="data-item">{{ statisticsObject.rate }}%</span>
          </el-descriptions-item>
          <el-descriptions-item label="总入款金额：">
            <div>
              <span class="data-item" v-if="statisticsObject.totalIncomeRMB"
                >{{ statisticsObject.totalIncomeRMB }}(RMB)</span
              >
              <span
                v-if="
                  statisticsObject.totalIncomeRMB &&
                  statisticsObject.totalIncomeUSDT
                "
                >+</span
              >
              <span class="data-item" v-if="statisticsObject.totalIncomeUSDT">
                {{ statisticsObject.totalIncomeUSDT }}(USDT)</span
              >
            </div>
          </el-descriptions-item>
          <el-descriptions-item label="应下发：">
            <div>
              <span class="data-item" v-if="statisticsObject.PlanTotalRMB"
                >{{ statisticsObject.PlanTotalRMB }}(RMB)</span
              >
              <span
                v-if="
                  statisticsObject.PlanTotalRMB &&
                  statisticsObject.PlanTotalUSDT
                "
                >+</span
              >
              <span class="data-item" v-if="statisticsObject.PlanTotalUSDT">
                {{ statisticsObject.PlanTotalUSDT }}(USDT)</span
              >
            </div>
          </el-descriptions-item>
          <el-descriptions-item label="已下发：">
            <div>
              <span class="data-item" v-if="statisticsObject.issuedRMB"
                >{{ statisticsObject.issuedRMB }}(RMB)</span
              >
              <span
                v-if="statisticsObject.issuedRMB && statisticsObject.issuedUSDT"
                >+</span
              >
              <span class="data-item" v-if="statisticsObject.issuedUSDT">
                {{ statisticsObject.issuedUSDT }}(USDT)</span
              >
            </div>
          </el-descriptions-item>
          <el-descriptions-item label="未下发：">
            <div>
              <span class="data-item" v-if="statisticsObject.unissuedRMB"
                >{{ statisticsObject.unissuedRMB }}(RMB)</span
              >
              <span
                v-if="
                  statisticsObject.unissuedRMB && statisticsObject.unissuedUSDT
                "
                >+</span
              >
              <span class="data-item" v-if="statisticsObject.unissuedRMB">
                {{ statisticsObject.unissuedUSDT }}(USDT)</span
              >
            </div>
          </el-descriptions-item>
        </el-descriptions>
      </div>
    </div>
    <div class="btn-item-box">
      <van-button type="info" class="btn-info" @click="setDayHandle('previous')">上一天</van-button>
      <span class="btn-text" @click="getListshandle()">查看全部账单</span>
      <van-button type="info" class="btn-info" @click="setDayHandle('next')">下一天</van-button>
    </div>
    <div class="bottom-box">
      <span>TG记账机器人 - 官方团队：@AI678</span>
    </div>
    <van-popup v-model="show" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="dateminute"
        :title="title"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="confirmTimeHandler"
        @cancel="show = false"
      />
    </van-popup>
  </div>
</template>

<script>

import { billMixin } from '@/mixins/billMixin'
export default {
  mixins: [billMixin]
};
</script>
<style lang="scss" scoped>
.wapper-box {
  padding: 10px 0 10px;
  max-width: 1200px;
  margin: 0 auto;
}
.search-box {
  margin-bottom: 15px;
  padding: 0 10px;
}
.time-box {
  flex: 1;
  display: flex;
  margin-bottom: 10px;
}
.start-time {
  flex: 1;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  font-size: 14px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  background: #dfdede;
}
.icon-zhong {
  width: 16px;
  height: 16px;
  margin-right: 5px;
}
.time-text-box {
  flex: 1;
}
.start-time:last-child {
  margin-right: 0;
}
.placeholder-text {
  color: #999;
}
.search-input {
  flex: 1;
  display: flex;
}
.input-box {
  flex: 1;
}
.input-text {
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  height: 30px;
  line-height: 30px;
  outline: none;
  padding: 0 10px;
  width: 100%;
  font-size: 14px;
  border-right: none;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.popover-text {
  width: 80px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #ccc;
  margin-right: 8px;
  font-size: 14px;
  box-sizing: border-box;
  border-left: none;
  background: #d6d6d6;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.search-btn {
  height: 30px;
}
.table-box {
  padding: 0 10px;
  margin-bottom: 15px;
}
.table-title {
  color: #0283EF;
  font-size: 18px;
  margin-bottom: 10px;
}
.tatol-title {
  color: #0283EF;
  font-size: 16px;
  margin-bottom: 10px;
  font-weight: 600;
}
.total-item {
  margin-bottom: 8px;
  color: #333;
  font-size: 14px;
}
.total-label {
  display: inline-block;
}
.btn-box {
  margin-top: 10px;
  padding: 0 10px;
}
.uploadBtn {
  color: #259dff;
  cursor: pointer;
  line-height: 32px;
  margin-left: 10px;
  font-size: 14px;
}
.descriptions-box {
  max-width: 500px;
}
.btn-item-box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  margin-top: 20px;
}
.btn-info {
  height: 32px;
}
.btn-text {
  color: #259dff;
  cursor: pointer;
  font-size: 14px;
  margin: 0 16px;
  background: #dbefff;
  height: 32px;
  line-height: 32px;
  padding: 0 10px;
  border-radius: 4px;
}
.bottom-box {
  margin: 50px 0 10px;
  text-align: center;
  color: #c9c9c9;
  font-size: 12px;
}
@media screen and (min-width: 600px) {
  .search-box {
    display: flex;
  }
  .search-input {
    margin-left: 10px;
  }
}

.data-item {
  padding: 5px 10px;
  background: #ebf8f6;
  color: #36b29e;
  border-radius: 4px;
  margin: 0 5px;
  font-size: 12px;
}
</style>
<style lang="scss">
.el-table {
  font-size: 12px;
}
.el-table .el-table__cell {
  padding: 5px 0;
}
.el-empty__image {
  width: 60px;
}
.el-empty__description {
  margin-top: 0;
}
.van-cell {
  padding-left: 0;
  padding-right: 0;
}
.van-button--info {
  background-color: #259dff;
  border: 1px solid #259dff;
  border-radius: 4px;
}
.van-collapse-item__content {
  padding-left: 0;
  padding-right: 0;
}
.el-table th.el-table__cell.is-leaf {
  background: #eee;
}

/* 整个滚动条的宽度 */
::-webkit-scrollbar {
  width: 6px; /* 对于垂直滚动条 */
  height: 6px; /* 对于水平滚动条 */
}

/* 滚动条轨道的样式 */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

/* 滚动条滑块的样式 */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 3px;

  /* 当鼠标悬停时改变滑块的颜色 */
  &:hover {
    background: #555;
  }
}

/* 对于火狐浏览器的滚动条样式 */
.scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1;
}
.van-cell__right-icon {
  display: none;
}
.collapse-tilte {
  display: flex;
  justify-content: space-between;
}
.collapse-icon {
  font-size: 12px;
  color: #0283EF;
}
</style>
